/* 路由切换动画 */
.slide-left-enter{
  position:absolute !important;
  top:0;
  left:0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
  z-index: 1000;
}
.slide-move-leave-active{
  position:absolute !important;
  top:0;
  left:0;
  -webkit-transition: 0s ease;
  transition: 0s ease;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  z-index: 2000;
}
.slide-move-enter{
  position:absolute !important;
  top:0;
  left:0;
  -webkit-transition: 0s ease;
  transition: 0s ease;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  z-index: 3000;
}
.slide-left-leave-active{
  position:absolute !important;
  top:0;
  left:0;
  -webkit-transition: 0s .2s ease;
  transition: 0s .2s ease;
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100%, 0);
  z-index: 2000;
}
.slide-right-leave-active{
  position:absolute !important;
  top:0;
  left:0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
  -webkit-transition: 0s .2s ease;
  transition: 0s .2s ease;
  z-index: 2000;
}
.slide-right-enter {
  position:absolute !important;
  top:0;
  left:0;
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100%, 0);
  z-index: 1000;
}

